import Layout from "../../../components/layout/Layout";
import { useGetStudentReportQuery } from "../../../controller/api/tahfiz/ApiReport";
import { useSelector } from "react-redux";
import LoadingScreen from "../../../components/loader/LoadingScreen";

const ParentTahfiz = () => {
  const { user } = useSelector((state) => state.auth);

  const { data, isLoading } = useGetStudentReportQuery(user.student_id, {
    skip: !user.student_id,
  });

  if (isLoading) {
    return <LoadingScreen />;
  }

  if (!data) {
    return (
      <Layout title="Laporan Tahfiz" levels={"parent"}>
        <div className="text-center py-5">
          <p className="text-muted">Tidak ada data laporan tahfiz</p>
        </div>
      </Layout>
    );
  }

  return (
    <Layout title="Laporan Tahfiz" levels={"parent"}>
      <div className="container-fluid p-4">
        {/* Student Information */}
        <div className="card shadow-sm mb-4">
          <div className="card-body">
            <h2 className="card-title h4 mb-4 text-primary">Informasi Siswa</h2>
            <div className="row">
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">Nama</label>
                <p className="h6 mb-0 fw-bold">{data.student_name}</p>
              </div>
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">NIS</label>
                <p className="h6 mb-0 fw-bold">{data.student_nis}</p>
              </div>
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">Kelas</label>
                <p className="h6 mb-0 fw-bold">{data.class}</p>
              </div>
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">Grade</label>
                <p className="h6 mb-0 fw-bold">{data.grade}</p>
              </div>
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">Homebase</label>
                <p className="h6 mb-0 fw-bold">{data.homebase}</p>
              </div>
              <div className="col-md-6 col-lg-4 mb-3">
                <label className="form-label text-muted small">Periode</label>
                <p className="h6 mb-0 fw-bold text-info">{data.periode}</p>
              </div>
            </div>
          </div>
        </div>

        {/* Target Information for All Grades */}
        <div className="card shadow-sm mb-4">
          <div className="card-body">
            <h2 className="card-title h4 mb-4 text-info">
              Target Hafalan Semua Tingkat
            </h2>
            <div className="row">
              {data.targets_by_grade?.map((gradeData, gradeIndex) => (
                <div key={gradeIndex} className="col-12 mb-4">
                  <div className="card border-info">
                    <div className={`card-header ${gradeData.grade_name === data.grade ? 'bg-info text-white' : 'bg-light'}`}>
                      <h5 className="card-title mb-0">
                        {gradeData.grade_name === data.grade && (
                          <i className="bi bi-star-fill me-2"></i>
                        )}
                        {gradeData.grade_name}
                        {gradeData.grade_name === data.grade && (
                          <span className="badge bg-warning text-dark ms-2">Tingkat Anda</span>
                        )}
                      </h5>
                    </div>
                    <div className="card-body">
                      <div className="row">
                        {gradeData.targets.map((target, targetIndex) => (
                          <div key={targetIndex} className="col-lg-3 col-md-4 col-sm-6 mb-3">
                            <div className="card border-secondary h-100">
                              <div className="card-header bg-secondary text-white text-center">
                                <h6 className="card-title mb-0">{target.juz}</h6>
                              </div>
                              <div className="card-body text-center">
                                <div className="row">
                                  <div className="col-6">
                                    <div className="text-muted small">Ayat</div>
                                    <div className="fw-bold">{target.total_verses}</div>
                                  </div>
                                  <div className="col-6">
                                    <div className="text-muted small">Baris</div>
                                    <div className="fw-bold">{target.total_lines}</div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Memorization Progress */}
        <div className="card shadow-sm mb-4">
          <div className="card-body">
            <h2 className="card-title h4 mb-4 text-primary">
              Progress Target Hafalan
            </h2>
            <div className="row">
              {data.memorization?.map((item, index) => (
                <div key={index} className="col-lg-4 col-md-6 mb-4">
                  <div className="card border-primary h-100">
                    <div className="card-header bg-primary text-white">
                      <h5 className="card-title mb-0">{item.juz}</h5>
                    </div>
                    <div className="card-body">
                      {/* Progress Bar */}
                      <div className="mb-3">
                        <div className="d-flex justify-content-between align-items-center mb-2">
                          <span className="text-muted small">Progress</span>
                          <span className="fw-bold">{item.progress}%</span>
                        </div>
                        <div className="progress" style={{ height: "8px" }}>
                          <div
                            className="progress-bar bg-primary"
                            role="progressbar"
                            style={{
                              width: `${Math.min(item.progress, 100)}%`,
                            }}
                            aria-valuenow={item.progress}
                            aria-valuemin="0"
                            aria-valuemax="100"
                          ></div>
                        </div>
                      </div>

                      {/* Details */}
                      <div className="row text-center">
                        <div className="col-4">
                          <div className="border-end">
                            <div className="text-muted small">Ayat</div>
                            <div className="fw-bold">
                              {item.completed}/{item.verses}
                            </div>
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="border-end">
                            <div className="text-muted small">Baris</div>
                            <div className="fw-bold">
                              {item.completed_lines}/{item.lines}
                            </div>
                          </div>
                        </div>
                        <div className="col-4">
                          <div className="text-muted small">Sisa</div>
                          <div className="fw-bold text-danger">
                            {item.uncompleted} ayat
                          </div>
                        </div>
                      </div>

                      {/* Surah Details */}
                      {item.surah && item.surah.length > 0 && (
                        <div className="mt-3">
                          <h6 className="text-muted mb-2">
                            Surah yang dipelajari:
                          </h6>
                          <div className="list-group list-group-flush">
                            {item.surah.map((surah, idx) => (
                              <div
                                key={idx}
                                className="list-group-item px-0 py-1 small"
                              >
                                <i className="bi bi-book me-2"></i>
                                {surah.surah_name} (ayat {surah.verse}, baris{" "}
                                {surah.line})
                              </div>
                            ))}
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>

        {/* Exceed Progress */}
        {data.exceed && data.exceed.length > 0 && (
          <div className="card shadow-sm">
            <div className="card-body">
              <h2 className="card-title h4 mb-4 text-success">
                Hafalan yang melebihi target
              </h2>
              <div className="row">
                {data.exceed.map((item, index) => (
                  <div key={index} className="col-lg-4 col-md-6 mb-4">
                    <div className="card border-success h-100">
                      <div className="card-header bg-success text-white">
                        <h5 className="card-title mb-0">
                          <i className="bi bi-star-fill me-2"></i>
                          {item.juz}
                        </h5>
                      </div>
                      <div className="card-body">
                        {/* Progress Bar */}
                        <div className="mb-3">
                          <div className="d-flex justify-content-between align-items-center mb-2">
                            <span className="text-muted small">Progress</span>
                            <span className="fw-bold">{item.progress}%</span>
                          </div>
                          <div className="progress" style={{ height: "8px" }}>
                            <div
                              className="progress-bar bg-success"
                              role="progressbar"
                              style={{
                                width: `${Math.min(item.progress, 100)}%`,
                              }}
                              aria-valuenow={item.progress}
                              aria-valuemin="0"
                              aria-valuemax="100"
                            ></div>
                          </div>
                        </div>

                        {/* Details */}
                        <div className="row text-center">
                          <div className="col-4">
                            <div className="border-end">
                              <div className="text-muted small">Ayat</div>
                              <div className="fw-bold">
                                {item.completed}/{item.verses}
                              </div>
                            </div>
                          </div>
                          <div className="col-4">
                            <div className="border-end">
                              <div className="text-muted small">Baris</div>
                              <div className="fw-bold">
                                {item.completed_lines}/{item.lines}
                              </div>
                            </div>
                          </div>
                          <div className="col-4">
                            <div className="text-muted small">Sisa</div>
                            <div className="fw-bold text-danger">
                              {item.uncompleted} ayat
                            </div>
                          </div>
                        </div>

                        {/* Surah Details */}
                        {item.surah && item.surah.length > 0 && (
                          <div className="mt-3">
                            <h6 className="text-muted mb-2">
                              Surah yang dipelajari:
                            </h6>
                            <div className="list-group list-group-flush">
                              {item.surah.map((surah, idx) => (
                                <div
                                  key={idx}
                                  className="list-group-item px-0 py-1 small"
                                >
                                  <i className="bi bi-book me-2"></i>
                                  {surah.surah_name} (ayat {surah.verse}, baris{" "}
                                  {surah.line})
                                </div>
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
};

export default ParentTahfiz;
